@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-display: swap;
}
body {
  background-color: #f2f2f2;
  color: #000000;
  font-family: "PT Sans", "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  margin: 0;
}
.container {
  box-shadow: 0 5px 15px rgba(0, 1, 1, 0.2);
  position: relative;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
.button {
  font-family: "PT Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  color: #ffffff;
  background-color: #81b3d2;
  border: none;
  cursor: pointer;
}
.button:hover {
  background-color: #669ec0;
}
.button:active {
  background-color: #5496bd;
  color: rgba(255, 255, 255, 0.3);
}
.button:disabled {
  background-color: #e5e5e5;
}
.main-header {
  position: relative;
  display: flex;
  flex-direction: column;
  background-color: #ffffff;
  color: #000000;
}
.main-header__logo {
  height: 96px;
  position: absolute;
  top: 0;
  left: 67px;
  z-index: 2;
}
@media (min-width: 768px) {
  .main-header__logo {
    z-index: 2;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
  }
}
.main-navigation {
  position: relative;
  font-family: "PT Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-align: center;
  min-height: 56px;
}
.main-navigation__list {
  list-style: none;
  margin: 0;
  padding: 0;
  background-color: #81b3d2;
}
.main-navigation__link {
  position: relative;
  display: block;
  padding: 17px 83px 12px 81px;
  text-transform: uppercase;
  text-decoration: none;
  color: #ffffff;
  box-shadow: 0 1px 0 rgba(1, 1, 1, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.2);
}
.main-navigation__toggle {
  position: absolute;
  width: 23px;
  height: 22px;
  top: 10px;
  right: 12px;
  display: block;
  cursor: pointer;
  border: none;
  background-color: #ffffff;
}
.main-navigation--closed .main-navigation__toggle::before {
  content: "";
  position: absolute;
  top: 8px;
  left: 0;
  width: 23px;
  height: 4px;
  background-color: #81b3d2;
  box-shadow: 0 9px 0 0 #81b3d2, 0 18px 0 0 #81b3d2;
}
.main-navigation--closed .main-navigation__toggle:hover::before {
  background-color: rgba(129, 179, 210, 0.6);
  box-shadow: 0 9px 0 0 rgba(129, 179, 210, 0.6), 0 18px 0 0 rgba(129, 179, 210, 0.6);
}
.main-navigation--closed .main-navigation__toggle:active::before {
  background-color: rgba(129, 179, 210, 0.3);
  box-shadow: 0 9px 0 0 rgba(129, 179, 210, 0.3), 0 18px 0 0 rgba(129, 179, 210, 0.3);
}
@media (max-width: 767px) {
  .main-navigation--opened .main-navigation__wrapper {
    position: absolute;
    width: 320px;
    z-index: 3;
  }
  .main-navigation--nojs .main-navigation__wrapper {
    position: static;
  }
}
.main-navigation--opened .main-navigation__toggle {
  background-color: #81b3d2;
  z-index: 4;
}
.main-navigation--opened .main-navigation__toggle::before,
.main-navigation--opened .main-navigation__toggle::after {
  content: "";
  position: absolute;
  top: 14px;
  left: 0;
  width: 22px;
  height: 4px;
  background-color: #ffffff;
}
.main-navigation--opened .main-navigation__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-navigation--opened .main-navigation__toggle::after {
  transform: rotate(-45deg);
}
.main-navigation--opened .main-navigation__toggle:hover::before,
.main-navigation--opened .main-navigation__toggle:hover::after {
  background-color: rgba(255, 255, 255, 0.6);
}
.main-navigation--opened .main-navigation__toggle:active::before,
.main-navigation--opened .main-navigation__toggle:active::after {
  background-color: rgba(255, 255, 255, 0.3);
}
@media (max-width: 767px) {
  .main-navigation--closed .main-navigation__list {
    display: none;
  }
  .main-navigation__link:hover {
    background-color: #669ec0;
  }
  .main-navigation__link:active {
    background-color: #5496bd;
    color: rgba(255, 255, 255, 0.3);
  }
}
.main-navigation--nojs .main-navigation__toggle {
  display: none;
}
@media (max-width: 767px) {
  .main-navigation--nojs .main-navigation__list {
    display: block;
    position: static;
  }
}
@media (min-width: 768px) {
  .main-navigation__toggle {
    display: none;
  }
  .main-navigation__list {
    display: grid;
    grid-template-columns: 153px 153px 153px 153px 153px;
    align-items: center;
    justify-content: space-between;
    background-color: #ffffff;
  }
  .main-navigation__item:nth-child(3) {
    grid-column: 4/5;
  }
  .main-navigation__link {
    color: #000000;
    box-shadow: none;
    padding: 0;
    padding-top: 18px;
    padding-bottom: 17px;
    font-size: 14px;
    line-height: 21px;
  }
  .main-navigation__link:hover {
    color: #81b3d2;
  }
  .main-navigation__link:active {
    color: rgba(0, 0, 0, 0.3);
  }
  .main-navigation__link--current {
    color: #766357;
  }
  .main-navigation__link--current:hover,
  .main-navigation__link--current :active {
    color: #766357;
  }
}
@media (min-width: 1200px) {
  .main-navigation__toggle {
    display: none;
  }
  .main-navigation__list {
    display: grid;
    grid-template-columns: 240px 240px 240px 240px 240px;
    align-items: center;
    justify-content: space-between;
    background-color: #ffffff;
  }
  .main-navigation__link {
    color: #000000;
  }
  .main-navigation__link--current {
    color: #766357;
  }
}
